<template>
  <div :transition="slide">
    <v-header>
      <div slot="left" class="h-left">
        <a href="javascript:history.go(-1)"><span class="close img"></span></a></div>
    </v-header>

    <div class="content">
      <form class="login-form" onsubmit="return false;">
        <div class="login-box opa-8">
          <div class="edit">
            <span class="img phone"></span>
            <span class="img delete" v-show="phone != ''" @click="deletePhone"></span>
            <input type="text" v-model="phone" placeholder="手机号" maxlength="11"
                   onkeyup="value=value.replace(/[^\d]/g,'')"/>
          </div>
          <div class="edit">
            <span class="img code"></span>
            <span class="get-code">
              <span class="color-main text" v-show="seconds == 0" @click="getCode">获取验证码</span>
              <span class="color-9 text" v-show="seconds > 0">{{seconds}}s后重发</span>
            </span>
            <input type="text" v-model="code" placeholder="请输入验证码"/>
          </div>
          <div class="edit">
            <span class="img password"></span>
            <span class="img eye close" @click="showPwd($event)"></span>
            <input type="password" v-model="password" placeholder="密码为6-20位字母数字组合" id="password"/>
          </div>
        </div>

        <input type="submit" value="注册" class="bg-color-main" @click="registerForm"/>
        <span class="register-remark remark opa-8">注册代表您已同意《用户注册协议》</span>
        <span class="login-remark remark opa-8">已有账号,<a v-link="{name:'login'}">立即登录</a></span>
      </form>
    </div>
  </div>
</template>
<script>
  import VHeader from '../components/Header'
  import $ from 'jquery'
  import {toast} from '../assets/js/toast'
  import {url} from '../const'
  import {st} from '../const'

  export default{
    route: {
      data () {
        if (st.has(st.keys.user)) {
          console.log('用户已登录');
          window.location.replace("#/user");
        }
      }
    },
    ready(){
      $("html").addClass("bg-img");
      $(".my-header .warp").addClass("replace");
    },
    data(){
      return {
        username: '',
        password: '',
        phone: '',
        code: '',
        seconds: 0
      }
    },
    components: {VHeader},
    methods: {
      getCode(){
        let vm = this;
        let reg = /^0?1[3|4|5|8][0-9]\d{8}$/;
        if (!vm.phone || !reg.test(vm.phone)) {
          toast.alert('请先输入正确的手机号');
          return;
        }
        if (vm.seconds == 0) {
          vm.seconds = 60;
        }
        let sec = setInterval(function () {
          vm.seconds = vm.seconds - 1;
        }, 1000);
        let s = vm.$watch('seconds', function () {
          if (vm.seconds == 0) {
            clearInterval(sec);
            s();
          }
        });
      },
      registerForm(){
        let errorMsg;
        if (!this.password) {
          errorMsg = "密码不能为空";
        }
        if (!this.code) {
          errorMsg = "验证码不能为空";
        }
        let reg = /^0?1[3|4|5|8][0-9]\d{8}$/;
        if (!this.phone || !reg.test(this.phone)) {
          toast.alert('请输入正确的手机号');
          return;
        }
        if (errorMsg) {
          toast.alert(errorMsg);
          return false;
        }
        this.$http.get(url.register,
          {
            username: this.username,
            password: this.password,
            phone: this.phone,
            code: this.code,
            loginType: this.loginType,
            authencationType: 'AJAX'
          }).then(
          function ({data}) {
            if (data.success) {
              toast.alert("注册成功");
              console.log(data.userId + " username:" + data.username);
              window.location.replace("#/login");
            } else {
              toast.alert(data.resultMsg);
            }
          }
        );
      },
      deletePhone(){
        this.phone = '';
      },
      showPwd(event){
        let obj = $(event.currentTarget);
        if (obj.is(".close")) {
          obj.removeClass("close").addClass("open");
          $("#password").attr("type", "text");
        } else {
          obj.removeClass("open").addClass("close");
          $("#password").attr("type", "password");
        }
      }
    }
  }
</script>
<style>
  .bg-img {
    background: url(../assets/img/bg/login-bg.jpg);
    background-size: 100%;
  }

  .my-header .warp.replace {
    background-color: #9e9e9e;
  }

  .my-header .close {
    background-image: url(../assets/img/icon/close.png);
  }

  .content {
    padding: .44rem .15rem 0 .15rem;
  }

  .login-form {
    margin-top: .35rem;
    position: relative;
  }

  .edit .get-code {
    position: absolute;
    right: .1rem;
    top: .12rem;
  }

  .get-code .text {
    border-left: .01rem solid #dadada;
    padding-left: .1rem;
    height: .2rem;
    display: block;
  }

  .login-form input[type='submit'] {
    border: none;
    width: 100%;
    height: .4rem;
    color: #fff;
    border-radius: .05rem;
    margin-top: .25rem;
    margin-bottom: .25rem;
    font-size: .16rem;
  }

  .login-form .link {
    color: #dadada;
    position: absolute;
    bottom: 0;
  }

  .login-form .sign-up {
    left: .1rem;
  }

  .login-form .forget-pwd {
    right: .1rem;
  }

  .img.user {
    background-image: url(../assets/img/icon/user.png);
  }

  .img.password {
    background-image: url(../assets/img/icon/lock.png);
  }

  .img.phone {
    background-image: url(../assets/img/icon/phone02.png);
  }

  .img.code {
    background-image: url(../assets/img/icon/security.png);
  }

  .img.delete {
    background-image: url(../assets/img/icon/close02.png);
    right: .1rem;
    top: .15rem;
    width: .15rem;
    height: .15rem;
    background-size: .15rem;
  }

  .eye.close {
    background-image: url(../assets/img/icon/eye-close.png);
  }

  .eye.open {
    background-image: url(../assets/img/icon/eye-open.png);
  }
</style>

<style scoped>
  .login-form .remark {
    display: block;
    color: #fff;
    text-align: center;
  }

  .login-form .remark a {
    color: #fff;
  }

  .edit input[type='text'], .edit input[type='password'] {
    border: none;
    width: 100%;
    height: 100%;
    text-indent: .25rem;
    font-size: .14rem;
  }

  .edit .img {
    position: absolute;
    width: .22rem;
    height: .22rem;
    background-size: .22rem;
    top: .1rem;
  }

  .img.eye {
    right: .1rem;
    top: .18rem;
    width: .16rem;
    height: .11rem;
    background-size: .16rem;
  }

  .login-box {
    height: 1.35rem;
    background-color: #fff;
    border-radius: .05rem;
  }

  .login-box .edit {
    border-bottom: 1px solid #e5e5e5;
  }

  .login-box .edit:last-child {
    border-bottom: none;
  }

  .login-box .edit {
    margin-left: .1rem;
    padding-right: .1rem;
    position: relative;
    height: 33.3%;
  }
</style>



